.contain {
  width: 100%;
  height: 200px;
  display: block;
  position: relative;
  box-shadow: 5px 8px 12px rgb(44, 44, 44);
  transition: all 0.3s ease-in-out;
  display: inline-block;
}

.searchbar {
  display: flex;
  flex-wrap: wrap;
}

.search {
  margin: 10px;
  width: 30%;
}

.created {
  margin-top: 30px;
  margin-left: 60px;
  position: relative;
  text-decoration: none;
  color: white;
}

.divContainer {
  margin-top: 30px;
  margin-left: 60px;
  width: 30%;
  border-radius: 3px;
  color: white;
  justify-content: end;
  display: block;
  box-sizing: border-box;
  padding: 3px;
}

.link {
  width: 30%;
  margin: 0;
  text-decoration: none;
}

.filters {
  display: flex;
  margin: 3px;
}

.label {
  width: 80%;
  color: rgb(252, 252, 252);
  text-align: left;
}

.option {
  margin-left: 30%;
}

.btn button {
  width: 100px;
  height: 25px;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  background-color: #f9dc02;
  border-radius: 5px;
  margin: 8px 15px;
  cursor: pointer;
}

.gif {
  margin-top: 30%;
  margin-left: 23%;
  width: 50%;
  height: 80%;
  border-radius: 46% 54% 48% 52% / 63% 86% 14% 37%;
  box-shadow: 0 8px 10px rgba(251, 252, 252, 0.31);
}